import Layout from '../comps/Layout'
import Link from 'next/link'

const PostLink = props => (
    <li>
        <Link href={`/pages/post?title=${props.title}`}>
            <a>{props.title}</a>
        </Link>
    </li>
)

const Index = () => (
    <Layout>
        <h1>Hahah</h1>
        <ul>
            <PostLink title="sad"/>
            <PostLink title="dudu"/>
        </ul>
    </Layout>
)

export default Index